"use client";

import { Testimonials as TestimonialsSection, VisionStatement as VisionMissionSection, WeThriveComponent as ServicesShowcase } from "@/components/sections";
import { useEffect, useRef, useState } from "react";

export default function About() {
  const [isHeroVisible, setIsHeroVisible] = useState(false);
  const [isStatsVisible, setIsStatsVisible] = useState(false);
  const [isDescriptionVisible, setIsDescriptionVisible] = useState(false);
  const [showImages, setShowImages] = useState<number[]>([]);
  const [showTitleHighlight, setShowTitleHighlight] = useState(false);
  const [scrollProgress, setScrollProgress] = useState(0);
  const [hasAnimatedHero, setHasAnimatedHero] = useState(false);
  const [hasAnimatedStats, setHasAnimatedStats] = useState(false);
  const [hasAnimatedDescription, setHasAnimatedDescription] = useState(false);
  const [statsCounts, setStatsCounts] = useState({
    uptime: 0,
    messages: 0,
    years: 0,
    businesses: 0
  });
  const heroRef = useRef<HTMLDivElement>(null);
  const statsRef = useRef<HTMLDivElement>(null);
  const descriptionRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    // Hero section animation
    const heroObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && !hasAnimatedHero) {
            setIsHeroVisible(true);
            setHasAnimatedHero(true);
            // Animate images one by one
            setTimeout(() => setShowImages([0]), 500);
            setTimeout(() => setShowImages([0, 1]), 1000);
            setTimeout(() => setShowImages([0, 1, 2]), 1500);
            // Animate title highlight
            setTimeout(() => setShowTitleHighlight(true), 2000);
          }
        });
      },
      { threshold: 0.2 }
    );

    // Stats section animation
    const statsObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && !hasAnimatedStats) {
            setIsStatsVisible(true);
            setHasAnimatedStats(true);
            // Animate counters
            const duration = 2000;
            const steps = 60;
            const interval = duration / steps;
            
            let currentStep = 0;
            const timer = setInterval(() => {
              currentStep++;
              const progress = currentStep / steps;
              
              setStatsCounts({
                uptime: Math.round(99.99 * progress * 100) / 100,
                messages: Math.round(10000000 * progress),
                years: Math.round(5 * progress),
                businesses: Math.round(5000 * progress)
              });
              
              if (currentStep >= steps) {
                clearInterval(timer);
                setStatsCounts({
                  uptime: 99.99,
                  messages: 10000000,
                  years: 5,
                  businesses: 5000
                });
              }
            }, interval);

            return () => clearInterval(timer);
          }
        });
      },
      { threshold: 0.2 }
    );

    // Description section animation
    const descriptionObserver = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting && !hasAnimatedDescription) {
            setIsDescriptionVisible(true);
            setHasAnimatedDescription(true);
          }
        });
      },
      { threshold: 0.2 }
    );

    // Scroll progress indicator
    const handleScroll = () => {
      const totalHeight = document.documentElement.scrollHeight - window.innerHeight;
      const progress = (window.scrollY / totalHeight) * 100;
      setScrollProgress(progress);
    };

    const currentHeroRef = heroRef.current;
    const currentStatsRef = statsRef.current;
    const currentDescriptionRef = descriptionRef.current;

    if (currentHeroRef) heroObserver.observe(currentHeroRef);
    if (currentStatsRef) statsObserver.observe(currentStatsRef);
    if (currentDescriptionRef) descriptionObserver.observe(currentDescriptionRef);
    
    window.addEventListener('scroll', handleScroll);

    return () => {
      if (currentHeroRef) heroObserver.unobserve(currentHeroRef);
      if (currentStatsRef) statsObserver.unobserve(currentStatsRef);
      if (currentDescriptionRef) descriptionObserver.unobserve(currentDescriptionRef);
      window.removeEventListener('scroll', handleScroll);
    };
  }, [hasAnimatedHero, hasAnimatedStats, hasAnimatedDescription]);

  return (
    <main className="pt-20 overflow-hidden relative">
      {/* Scroll Progress Indicator */}
      <div className="fixed top-0 left-0 h-1 bg-blue-600 z-50 transition-all duration-300" style={{ width: `${scrollProgress}%` }} />

      {/* Hero Section with Animated Images */}
      <div ref={heroRef} className={`relative min-h-screen flex items-center justify-center px-4 py-20 ${isHeroVisible ? 'opacity-100' : 'opacity-0'}`} style={{ transition: 'opacity 1s ease-in-out' }}>
        <div className="relative max-w-7xl w-full">
          {/* Main content */}
          <div className="text-center relative z-10 space-y-8">
            <h1 className="text-5xl md:text-7xl font-bold mb-6 leading-tight">
              About&nbsp;
              <span className={`inline-block relative ${showTitleHighlight ? 'text-blue-600' : ''}`} style={{ transition: 'color 0.5s ease' }}>
                IntarVAS
                {showTitleHighlight && (
                  <span className="absolute bottom-0 left-0 w-full h-2 bg-blue-600 opacity-20 animate-pulse" />
                )}
              </span>
            </h1>
            <p className="text-xl md:text-2xl text-gray-600 max-w-3xl mx-auto">
              Transforming Business Communication Across Nigeria
            </p>
          </div>

          {/* Animated floating images */}
          <div className="absolute inset-0 pointer-events-none">
            {[
              { src: "/images/hero-intarvas.jpg", className: "top-10 left-10 w-40 h-40 rotate-6", delay: 0 },
              { src: "/images/pbx-analytics.jpg", className: "top-20 right-10 w-48 h-48 -rotate-3", delay: 500 },
              { src: "/images/crm-dashboard.jpg", className: "bottom-20 left-20 w-44 h-44 rotate-3", delay: 1000 }
            ].map((img, index) => (
              <div
                key={index}
                className={`absolute ${img.className} rounded-2xl shadow-2xl overflow-hidden ${
                  showImages.includes(index) ? 'opacity-100 scale-100' : 'opacity-0 scale-50'
                }`}
                style={{
                  transition: `all 0.8s cubic-bezier(0.34, 1.56, 0.64, 1) ${img.delay}ms`,
                  animation: showImages.includes(index) ? 'float 6s ease-in-out infinite' : 'none'
                }}
              >
                <img src={img.src} alt={`IntarVAS ${index + 1}`} className="w-full h-full object-cover" />
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Stats Section */}
      <div ref={statsRef} className={`py-20 px-4 bg-gradient-to-br from-blue-50 to-white ${isStatsVisible ? 'opacity-100' : 'opacity-0'}`} style={{ transition: 'opacity 1s ease' }}>
        <div className="max-w-7xl mx-auto">
          <h2 className="text-4xl font-bold text-center mb-16">Our Impact in Numbers</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-8">
            {[
              { value: `${statsCounts.uptime}%`, label: "Uptime" },
              { value: `${(statsCounts.messages / 1000000).toFixed(1)}M+`, label: "Messages Delivered" },
              { value: `${statsCounts.years}+`, label: "Years of Excellence" },
              { value: `${statsCounts.businesses}+`, label: "Businesses Served" }
            ].map((stat, index) => (
              <div
                key={index}
                className={`text-center p-8 rounded-2xl bg-white shadow-lg ${
                  isStatsVisible ? 'translate-y-0 opacity-100' : 'translate-y-10 opacity-0'
                }`}
                style={{
                  transition: `all 0.6s ease ${index * 0.1}s`
                }}
              >
                <div className="text-5xl font-bold text-blue-600 mb-2">{stat.value}</div>
                <div className="text-gray-600">{stat.label}</div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* Description Section */}
      <div ref={descriptionRef} className={`py-20 px-4 ${isDescriptionVisible ? 'opacity-100' : 'opacity-0'}`} style={{ transition: 'opacity 1s ease' }}>
        <div className="max-w-4xl mx-auto space-y-6 text-lg leading-relaxed text-gray-700">
          <p className={`${isDescriptionVisible ? 'translate-x-0 opacity-100' : '-translate-x-10 opacity-0'}`} style={{ transition: 'all 0.8s ease 0.2s' }}>
            IntarVAS is Nigeria's leading provider of business communication solutions. We specialize in Cloud PBX systems, bulk messaging, and vanity numbers that help businesses communicate more effectively with their customers.
          </p>
          <p className={`${isDescriptionVisible ? 'translate-x-0 opacity-100' : '-translate-x-10 opacity-0'}`} style={{ transition: 'all 0.8s ease 0.4s' }}>
            Founded on the principle that every business deserves enterprise-grade communication tools, we've grown to serve over 5,000 businesses across Nigeria, from startups to Fortune 500 companies.
          </p>
          <p className={`${isDescriptionVisible ? 'translate-x-0 opacity-100' : '-translate-x-10 opacity-0'}`} style={{ transition: 'all 0.8s ease 0.6s' }}>
            Our platform delivers 99.99% uptime and has processed over 10 million messages, making us the trusted choice for businesses that need reliable, scalable communication solutions.
          </p>
        </div>
      </div>

      <VisionMissionSection />
      <ServicesShowcase />
      <TestimonialsSection />

      <style jsx>{`
        @keyframes float {
          0%, 100% {
            transform: translateY(0px) rotate(var(--rotate));
          }
          50% {
            transform: translateY(-20px) rotate(var(--rotate));
          }
        }
      `}</style>
    </main>
  );
}

